let dataConvert = {
    //
    skyCon: {
        "CLEAR_DAY": "",
        "CLEAR_NIGHT": "",
        "PARTLY_CLOUDY_DAY": "",
        "PARTLY_CLOUDY_NIGHT": "",
        "CLOUDY": "",
        "LIGHT_HAZE": "",
        "MODERATE_HAZE": "",
        "HEAVY_HAZE": "",
        "LIGHT_RAIN": "",
        "MODERATE_RAIN": "",
        "HEAVY_RAIN": "",
        "STORM_RAIN": "",
        "FOG": "",
        "LIGHT_SNOW": "",
        "MODERATE_SNOW": "",
        "HEAVY_SNOW": "",
        "STORM_SNOW": "",
        "DUST": "",
        "SAND": "",
        "WIND": "",
        "THUNDER_SHOWER": "",
        "HAIL": "",
        "SLEET": "",
    },
    direction: (direction) => {
        let i = Math.round(direction / 22.5);
        let dName = ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""];
        return dName[i];
    },
    windSpeed: (speed) => {
        if (speed < 1) return "";
        if (speed < 20) return "";
        return "";
    },
    warning: (content) => {
        if (content.length === 0) return "";
        let type = ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""];
        let grade = ["", "", "", ""];
        let color = ["blue", "yellow", "orange", "red"];

        let result = [];
        for (let i in content) {
            if(content.hasOwnProperty(i)) {
                let code = content[i].code;
                let typeNum = Number(code.substring(0, 2)) - 1;
                let gradeNum = code[3] - 1;
                result.push([type[typeNum], grade[gradeNum], color[gradeNum]]);
            }
        }
        return result;
    },
};

let caiYunData = (weatherData) => {
    let r = weatherData.result.realtime;
    let h = weatherData.result.hourly;
    let d = weatherData.result.daily;
    let nowDate = new Date();
    let time = nowDate.getHours() + ":" + nowDate.getMinutes();
    let hourly=[],daily=[];
    let realtime = {
        updateTime: time,
        weatherData,
        keyPoint: weatherData.result.forecast_keypoint,//
        trend: h.description,//
        weather: [r.skycon, dataConvert.skyCon[r.skycon]],
        apparentTemperature: Math.round(r.apparent_temperature),//
        temperature: Math.round(r.temperature),//
        windSpeed: [Math.round(r.wind.speed), dataConvert.windSpeed(r.wind.speed)],//
        windDirection: [r.wind.direction, dataConvert.direction(r.wind.direction)],//
        uv: [r.life_index.ultraviolet.index, r.life_index.ultraviolet.desc],//
        aqi: [r.air_quality.aqi.chn, r.air_quality.description.chn],//
        visibility: Math.round(r.visibility),//
        humidity: Math.round(r.humidity * 100),//
        airPressure: Math.round(r.pressure / 1000),//
        cloudCover: r.cloudrate,//
        shortwaveRadiation: r.dswrf,//
        precipitationIntensity: r.precipitation.local.intensity,//
        closestPrecipitationDistance: Math.round(r.precipitation.nearest.distance),//
        closestPrecipitationIntensity: r.precipitation.nearest.intensity,//
        co: r.air_quality.co,
        no2: r.air_quality.no2,
        o3: r.air_quality.o3,
        pm10: r.air_quality.pm10,
        pm25: r.air_quality.pm25,
        so2: r.air_quality.so2,
        sun: [d.astro[0].sunrise.time, d.astro[0].sunset.time],
        warning: weatherData.result.alert.content.length > 0 ? [dataConvert.warning(weatherData.result.alert.content), weatherData.result.alert.content] : false,
        carWashing: d.life_index.carWashing[0].desc,
        coldRisk: d.life_index.coldRisk[0].desc,
    };

    //360
    for (let i = 24; i < 360; i++) {
        hourly.push({
            weather: [h.skycon[i].value, dataConvert.skyCon[h.skycon[i].value]],
            temperature: Math.round(h.temperature[i].value),
            aqi: Math.round(h.air_quality.aqi[i].value.chn),
        });
    }

    //16 + 
    for (let i = 0; i < 16; i++) {
        daily.push({
            weather: [d.skycon[i].value, dataConvert.skyCon[d.skycon[i].value]],
            temperatureRange: [Math.round(d.temperature[i].min), Math.round(d.temperature[i].max)],
            aqi: Math.round(d.air_quality.aqi[i].avg.chn),
        });
    }
    return {realtime,hourly, daily}
}
export {caiYunData}