import AMapLoader from "@amap/amap-jsapi-loader";
import {Message} from "..";

let initAMap = (containerRef, res, jsKey) => {
    AMapLoader.load({
        "key": jsKey || "3e1573216986096de00822a455a0a852",              // WebKey load 
        "version": "",   //  JSAPI  1.4.15
        "plugins": ['AMap.Geolocation'],           // 'AMap.Scale'
        "AMapUI": {             //  AMapUI
            "version": '1.1',   // AMapUI  1.1
            "plugins":[],       //  AMapUI ui
        },
    }).then((AMap)=>{
        let map;
        if(containerRef.current) {
            map = new AMap.Map(containerRef.current, {
                resizeEnable: true
            });

            let onComplete, onError;
            let geolocation = new AMap.Geolocation({
                enableHighAccuracy: true, //:true
                timeout: 10000, //10
                zoomToAccuracy: true, //false
                buttonPosition: 'RT',
                buttonOffset: new AMap.Pixel(10, 70),
            })
            geolocation.getCurrentPosition((status, result) => {
                if (status === 'complete') {
                    onComplete(result)
                } else {
                    onError(result)
                }
            });
            map.addControl(geolocation);

            // 
            let clickHandler = (e) => {
                Message('[ ' + e.lnglat.getLng() + ',' + e.lnglat.getLat() + ' ]', false, true);
            };
            map.on('click', clickHandler);

            //
            onComplete = (locationData) => {
                console.log("", locationData);
                locationData.ya_location = locationData.position.toString().split(',');
                locationData.ya_address = locationData.addressComponent.district + locationData.addressComponent.township
                res(locationData)
            };

            //
            onError = (data) => {
                alert('', data)
            }
        }else {
            console.log("containerRef.current")
        }
    }).catch(e => {
        console.log(e);
    })
};

export {initAMap}