import React, {useState} from 'react';
import "./richTextEditor.css";
import {Button, Icon, Input} from "../../index";

const RichTextBtnList = [
    {name: "undo", value: "", icon: "i-undo", title: ""},
    {name: "redo", value: "", icon: "i-redo", title: ""},
    {name: "removeFormat", value: "", icon: "i-removeFormat"},
    {name: "formatBlock", value: "<h1>", icon: "h1"},
    {name: "formatBlock", value: "<h2>", icon: "h2"},
    {name: "formatBlock", value: "<h3>", icon: "h3"},
    {name: "formatBlock", value: "<h4>", icon: "h4"},
    {name: "formatBlock", value: "<h4>", icon: "h4"},
    // {name: "fontName", value: "", icon: "i-font", title: ""},
    {name: "bold", value: "bold", icon: "i-bold", title: ""},
    {name: "italic", value: "", icon: "i-italic", title: ""},
    {name: "fontSize", value: 1, icon: "i-fontSize", title: ""},
    {name: "foreColor", value: "#009688", icon: "i-fColor", title: ""},
    {name: "backColor", value: "#daf5f1", icon: "i-background", title: ""},
    {name: "justifyCenter", value: "", icon: "i-justifyCenter", title: ""},
    {name: "justifyLeft", value: "", icon: "i-justifyLeft", title: ""},
    {name: "justifyRight", value: "", icon: "i-justifyRight", title: ""},
    {name: "formatBlock", value: "<blockquote>", icon: "i-quote", title: ""},
    // {name: "indent", value: "indent", icon: "i-leftIndent", title: ""},
    // {name: "outdent", value: "", icon: "i-rightIndent", title: ""},
    {name: "insertHorizontalRule", value: "", icon: "i-line", title: ""},
    {name: "strikeThrough", value: "", icon: "i-strikeThrough", title: ""},
    {name: "underline", value: "", icon: "i-underline", title: ""},
    {name: "superscript", value: "", icon: "i-superscript", title: ""},
    {name: "subscript", value: "", icon: "i-subscript", title: ""},
    {name: "insertOrderedList", value: "", icon: "i-ul", title: ""},
    {name: "insertUnorderedList", value: "", icon: "i-ol", title: ""},
    {name: "insertImage", value: "http://dummyimage.com/160x90", icon: "i-image", title: ""},
    {name: "createLink", value: "createLink", icon: "i-link", title: ""},
    {name: "unlink", value: "", icon: "i-unlink", title: ""},
    {name: "insertHTML", value: "<button >html </button>", icon: "i-html", title: "html"},
    {name: "blankLine", icon:"",value: "<div/>"},
    // {name: "insertParagraph", value: "", icon: "", title: ""},
    // {name: "justifyFull", value: "", icon: "", title: ""},
    // {name: "insertLineDown", value: "", icon: "", title: ""},
];

const RichTextEditor = (props) => {

    const [minimize, set_minimize] = useState(false);
    const [html, ser_html] = useState("");

    //
    let insertHtmlAtCaret = (html) => {
        let sel, range;
        if (window.getSelection) {
            // IE9 and non-IE
            sel = window.getSelection();
            if (sel.getRangeAt && sel.rangeCount) {
                range = sel.getRangeAt(0);
                range.deleteContents();
                // Range.createContextualFragment() would be useful here but is
                // non-standard and not supported in all browsers (IE9, for one)
                let el = document.createElement("div");
                el.innerHTML = html;
                let frag = document.createDocumentFragment(), node, lastNode;
                while ((node = el.firstChild)) {
                    lastNode = frag.appendChild(node);
                }
                range.insertNode(frag);
                // Preserve the selection
                if (lastNode) {
                    range = range.cloneRange();
                    range.setStartAfter(lastNode);
                    range.collapse(true);
                    sel.removeAllRanges();
                    sel.addRange(range);
                }
            }
        } else if (document.selection && document.selection.type !== "Control") {
            // IE < 9
            // document.selection.createRange().pasteHTML(html);
        }
    };

    //
    let execCommand = (btn) => {
        switch (btn.name) {
            case "insertLineDown" :
                insertHtmlAtCaret("</br>");
                break;
            case "fontSize" :
                document.execCommand("fontSize", true, 1);
                break;
            case "backColor"  :
                document.execCommand(btn.name, true, btn.value);
                break;
            case "blankLine"  :
                window.getSelection().focusNode.insertBefore(btn.value)
                break;
            default :
                document.execCommand(btn.name, false, btn.value);
        }
    }

    //html
    let htmlView = () => {

    }
    let btnList = [];
    if (minimize) {
        btnList.push(<Button icon={"i-BAI-shangjiantou"} onClick={() => set_minimize(!minimize)}/>)
    } else {
        btnList = RichTextBtnList.map((i, k) => {

            switch (i.name) {
                case "fontSize":
                    let dropData = ["12px","16px","18px","20px","24px"]
                    return <Input className="page radius6" type={"select"} dropDownBoxData={dropData}
                                   autoContent={true}
                                  onChange={execCommand.bind(this)} selectIcon={false}/>
                default:
                    let btnName = i.icon.indexOf("i-") > -1 ? <Icon name={i.icon}/> : i.icon;
                    return <button className={`ya-richTextBtn ${i.name}`} title={i.title} key={k}
                                   onClick={execCommand.bind(this, i)}>{btnName}</button>;
            }

        });
        btnList.push(<Button color={"red"}></Button>)
        btnList.push(<Button icon={"i-BAI-xiajiantou"} onClick={() => set_minimize(!minimize)}/>)
    }
    return <div>
        <div className="ya-content" contentEditable={true} dangerouslySetInnerHTML={{__html: props.content}}/>
        <div className="ya-richText">
            <span className="ya-richTextBtn html true" onClick={htmlView.bind(this)}> html</span>
            {html || btnList}
        </div>
    </div>
}

export {RichTextEditor};