import React, {Component} from 'react';
import {Button, Tree} from '../../../index';
import {testTreeData, textNodeAddList, testSelectedIdsData, getNavData} from '../../../base/Tree/TreeUtil';
import {Icon} from "../../../index";

/**
 * 
 */
class TreeDemo extends Component {

    //
    static defaultProps = {
        treeData: [], //
        selectedIds: [], //id
        getReturnData: ()=>{},//
    };

    constructor(props) {
        super(props);

        console.log("TreeDemo constructor");
        let treeData = testTreeData(5, 20);
        let selectedIds = testSelectedIdsData(treeData, 4);
        let fileTreeData = testTreeData(5, 20, "file");

        let menuData = testTreeData(2, 5);
        let menuSelectedIds = testSelectedIdsData(menuData, 1);
        this.state = {
            loading: true,
            treeData,
            fileTreeData,
            defaultSelectedIds: selectedIds,//
            menuData,
            menuSelectedIds,
            defaultOpenIds: selectedIds,//
            navData: [],

            selectedIds: selectedIds,//id
            openIds: selectedIds,//id
            nodeContentClick: {},
            addNodeClick: {},
            indexBarClick: {},
            updateNodes: [],
            defaultOpenLevel: 1,
        }
    }

    //node
    nodeContentClick = node => {
        this.setState({nodeContentClick: node});
    };
    //
    checkboxClick = selectedIds => {
        this.props.getReturnData(selectedIds);
        this.setState({selectedIds});
    };
    //
    openIconClick = openIds => {
        this.setState({openIds});
    };

    //
    updateNode() {
        let treeData = testTreeData(10, 10);
        let selectedIds = testSelectedIdsData(treeData, 2);
        this.setState({
            treeData,
            defaultSelectedIds: selectedIds,
            defaultOpenIds: selectedIds,
        })
    }

    //
    addNodeFun = (parentId) => {
        return textNodeAddList(parentId, 10);
    };

    //
    addNodeClick(node) {
        this.setState({addNodeClick: node});
    }

    //
    indexBarClick(node) {
        this.setState({indexBarClick: node});
    }

    //()
    updateNodes(node) {
        this.setState({updateNodes: node});
    }

    render() {
        return (
            <div className="ya-treeDemo">
                <div className="ya-console-area">
                    <h2 className="ya-title"></h2>
                    <Button color={"blue"} onClick={this.updateNode.bind(this)}></Button>
                    <br/>
                    <div> {JSON.stringify(this.state.defaultSelectedIds)}</div>
                    <div> {this.state.defaultOpenLevel}</div>
                    <div>:  {JSON.stringify(this.state.selectedIds)}</div>
                    <div>: {JSON.stringify(this.state.openIds)}</div>
                    <div>: {JSON.stringify(this.state.nodeContentClick)}</div>
                    <div> {JSON.stringify(this.state.addNodeClick)}</div>
                    <div> {JSON.stringify(this.state.indexBarClick)}</div>
                    <div> {JSON.stringify(this.state.updateNodes)}</div>
                </div>
                <div className="demo">
                    <h1 className="ya-title"></h1>
                    <h2 className="ya-title"></h2>
                    <Tree treeData={this.state.treeData}
                          selectedIds={this.state.defaultSelectedIds}
                          openLevel={this.state.defaultOpenLevel}
                          openIds={this.state.defaultOpenIds}

                          checkbox={true}
                          nodeIconBtn={true}
                          operationBtn={true}
                          indexBar={true}
                          autoSearch={true}
                          searchNum={12}
                          draggable={true}
                          theme={true}

                          openIcon={<Icon name="i-folder-open"/>}
                          closeIcon={<Icon name="i-folder"/>}
                          childIcon={<Icon name="i-file"/>}

                          addNodeFun={this.addNodeFun.bind(this)}
                          nodeContentClick={this.nodeContentClick.bind(this)}
                          checkboxClick={this.checkboxClick.bind(this)}
                          openIconClick={this.openIconClick.bind(this)}
                          addNodeClick={this.addNodeClick.bind(this)}
                          indexBarClick={this.indexBarClick.bind(this)}
                          updateNodes={this.updateNodes.bind(this)}

                          ref={tree => {
                              this.tree = tree
                          }}
                    />
                </div>
                <div className="demo">
                    <h2 className="ya-title">-</h2>
                    <Tree treeData={this.state.treeData}
                          selectedIds={this.state.defaultSelectedIds}
                          indexId={this.state.defaultOpenIds[0]}

                          treeType={2}
                          nodeIconBtn={true}
                          operationBtn={true}
                          theme={true}
                          indexBar={true}
                          autoSearch={true}
                          searchNum={11}
                          draggable={true}

                          closeIcon={<Icon name="i-folder"/>}

                          nodeContentClick={this.nodeContentClick.bind(this)}
                          openIconClick={this.openIconClick.bind(this)}
                          indexBarClick={this.indexBarClick.bind(this)}
                          updateNodes={this.updateNodes.bind(this)}

                          ref={tree => {
                              this.tree = tree
                          }}
                    />
                </div>
                <div className="demo">
                    <h2 className="ya-title">-</h2>
                    <Tree treeData={this.state.fileTreeData}
                          treeType={2}
                          displayMode={"grid"}

                          openBtn={false}
                          checkbox={true}
                          nodeIconBtn={true}
                          operationBtn={false}
                          theme={true}
                          indexBar={true}
                          autoSearch={true}
                          searchNum={11}
                          draggable={true}

                          nodeContentClick={this.nodeContentClick.bind(this)}
                          checkboxClick={this.checkboxClick.bind(this)}
                          openIconClick={this.openIconClick.bind(this)}
                          indexBarClick={this.indexBarClick.bind(this)}
                          updateNodes={this.updateNodes.bind(this)}

                          ref={tree => {
                              this.tree = tree
                          }}
                    />
                </div>
                <div className="demo">
                    <h2 className="ya-title"></h2>
                    <Tree treeData={this.state.menuData}
                          indexId={this.state.menuSelectedIds[0]}

                          treeType={4}
                          operationBtn={true}
                          openLevel={"all"}
                        // indent = {false}
                          menuStyle={"dark"}
                          rightClickMenu={true}

                          ref={tree => {
                              this.tree = tree
                          }}
                    />
                </div>
                <h2 className="ya-title"></h2>
                <Tree treeData={this.state.navData}

                      treeType={3}
                      openBtn={false}
                      openLevel={"all"}

                      ref={tree => {
                          this.tree = tree
                      }}
                />
                <div className="demo">
                    <h2 className="ya-title"></h2>
                    <Tree treeData={this.state.menuData}

                          treeType={1}
                          operationBtn={true}
                          theme={true}
                          indexBar={true}
                          autoSearch={true}
                          searchNum={11}
                          draggable={true}

                          childIcon={<Icon name="i-file"/>}
                          nodeContentClick={this.nodeContentClick.bind(this)}
                          openIconClick={this.openIconClick.bind(this)}
                          indexBarClick={this.indexBarClick.bind(this)}
                          updateNodes={this.updateNodes.bind(this)}

                          async={true}
                          asyncGetChildNode={this.asyncGetChildNode}
                          asyncSearchNode={this.asyncSearchNode}
                          ref={tree => {
                              this.tree = tree
                          }}
                    />
                </div>
                <div className="demo">
                    <h2 className="ya-title">[]</h2>
                    <Tree treeData={this.state.treeData}

                          operationBtn={true}
                          autoSearch={true}
                          searchNum={12}
                          addNodeSwitch={true}

                          addNodeFun={this.addNodeFun.bind(this)}
                          addNodeClick={this.addNodeClick.bind(this)}
                          indexBarClick={this.indexBarClick.bind(this)}

                          ref={tree => {
                              this.tree = tree
                          }}
                    />
                </div>

            </div>
        );
    }

    componentDidMount() {
        if (this.state.loading){
            let navData = getNavData();
            this.setState({navData, loading: false});
        }
    }

    //
    asyncGetChildNode(parentId) {
        return textNodeAddList(parentId, 10);
    }

    //
    asyncSearchNode(node) {
        return textNodeAddList(node.parentId, 10);
    }
}

export default TreeDemo