import React, {useEffect, useState} from "react";
import "./message.css";
import {Button, Message, getNavData, Tree} from "../../../index";
import ButtonDemo from "../Button/ButtonDemo";
import {Confirm} from "../../../base/Message/Message";

/**
 * 
 */
const MessageDemo = () => {

    //
    const [loading, setLoading] = useState(true);
    const [navData, setNavData] = useState([]);
    useEffect(() => {
        if (loading) {
            setNavData(getNavData())
            setLoading(false)
        }
    },[loading])
    return <div>
        <Tree treeData={navData}
              treeType={3}
              openBtn={false}
              openLevel={"all"}
        />
        <div className="ya-p">
            <h2 className="ya-title"></h2>
            <Button color={"blue"} onClick={() => Message("2",)}>{""}</Button>
        </div>
        <div className="ya-p">
            <h2 className="ya-title"></h2>
            <Button color={"blue"} onClick={() => Message("", false)}>{""}</Button>
        </div>

        <div className="ya-p">
            <h2 className="ya-title"></h2>
            <Button color={"blue"} onClick={() => Message("4000", 4000)}>{"4000"}</Button>
        </div>
        <div className="ya-p">
            <h2 className="ya-title"></h2>
            <Button color={"blue"}
                    onClick={() => Confirm({content: ""}, () => Message("Yes"), () => Message("No"))}>
                {""}
            </Button>
        </div>
        <div className="ya-p">
            <h2 className="ya-title"></h2>
            <Button color={"blue"} onClick={() => Message(<ButtonDemo/>, false)}>{""}</Button>
        </div>
    </div>
}

export default MessageDemo;